.App {
    max-width: 1440px;

    .App-header {
        position: sticky;
        top: 0;
        padding: 3.2rem 2.8%;
        background-color: white;
        z-index: 4;
        display: flex;
        justify-content: space-between;
        align-items: center;
        transition: transform 400ms;

        *{
            font-size: 2.4rem;
        }

        #toggle-lang {
           
            
            .lang-btn {
                background-color: transparent;
                border: none;

                &:hover {
                    cursor: pointer;
                    opacity: 0.8;
                }
            }
        }

        #navbar {
            .nav-link {
                border: 2px solid black;
                text-decoration: none;
                border-radius: 2.5rem;
                padding: 0.2rem 1.2rem 0.4rem;
            }
        }
    }

    main {
        padding: 0 2.8%;
        
        section {
            margin: auto;
            padding-top: 12rem;
            padding-bottom: 14rem;
        }

        h1, h2, h3 {
            font-weight: normal;
            font-size: 1em;
        }

        .uppercase {
            text-transform: uppercase;
        }

        #home {
            #hello {
                font-size: 10.9rem;
            }

            #description {
                font-size: 3.2rem;
                margin-top: 2.8rem;
                width: 70%;

                #email {
                    &:hover {
                        opacity: 0.8;
                    }
                }
            }
        }

        .section-title {
            font-size: 10.9rem;
            margin-bottom: 7.2rem;
        }

        #projects {
            &-gallery {
                display: grid;
                grid-template-columns: repeat(6, 1fr);
                grid-gap: 1.8rem;

                .project-link {
                    .project {
                        position: relative;
                        border-radius: 2.4rem;
                        height: 50rem;
                        overflow: hidden;
    
                        * {
                            color: white;
                            transition: 600ms ease;
                        }
    
                        &-name {
                            position: absolute;
                            top: 0;
                            z-index: 1;
                            width: 100%;
                            padding: 3.6rem 4rem 3.2rem;
                            font-size: 3.2rem;
                            text-transform: lowercase;

                            &-arrow {
                                width: 1.7rem;
                                height: 1.7rem;
                                margin-left: 2rem;
                            }
                        }
    
                        &-image {
                            width: 100%;
                            height: 100%;
                            border-radius: 2.4rem;
                            object-fit: cover;
                        }

                        &:hover *:not(.project-name) {  
                            transform: scale(1.05);
                        }
                    }
                }

                #photo-gallery-link {
                    grid-column: 1 / span 3;
                    grid-row: 1;

                    #photo-gallery {
                        background-color: #222222;

                        .project-image {
                            position: absolute;
                            bottom: 0;
                            right: 0;
                            padding-top: 9.2rem;
                            padding-left: 2.7rem;
                            z-index: 2;
                        }

                        &-text {
                            position: absolute;
                            top: 12rem;
                            left: 4rem;
                            font-size: 9.6rem;
                            z-index: 1;

                            .helvetica {
                                font-family: 'Helvetica';
                            }

                            #editorial {
                                font-family: 'Editorial', sans-serif;
                                font-size: 10.9rem;
                                text-transform: none;
                            }
                        }
                    }
                }

                #calculator-link {
                    grid-column: 1 / span 2;
                    grid-row: 2;

                    #calculator {
                        .project-name {
                            color: black;
                        }
                    }
                }

                #random-quote-machine-link {
                    grid-column: 4 / span 3;
                    grid-row: 1;
                }

                #technical-documentation-page-link {
                    grid-column: 3 / span 2;
                    grid-row: 2;

                    #technical-documentation-page {
                        .project-name {
                            color: black;
                            background-color: rgb(226,226,226);
                        }
                    }
                }

                #product-landing-page-link {
                    grid-column: 5 / span 2;
                    grid-row: 2;

                    #product-landing-page {
                        .project-name {
                            color: black;
                            background-color: rgb(226,226,226);
                        }
                    }
                }
            }

            #github-link {
                text-decoration: none;

                #github {
                    width: fit-content;
                    margin-top: 4rem;
                    padding: 2.6rem 3.6rem 2.8rem 2.8rem;
                    background-color: #f5f5f5;
                    border-radius: 2.4rem;
                    font-size: 3.2rem;

                    &-icon {
                        margin-right: 1.7rem;
                        padding-top: 0.1rem;
                    }

                    &-text {
                        border-bottom: 2px solid black;
                        display: inline-block;
                        line-height: 0.95;
                        position: relative;
                        top: -0.2rem;
                    }
                }  

                &:hover {
                    opacity: 0.7;
                }
            }
        }

        #technologies {
            &-flexbox {
                display: flex;
                flex-direction: column;
                flex-wrap: wrap;
                gap: 1.6rem;
                max-height: 60rem;
    
                .technologies-list {
                    font-style: normal;
                    padding: 4.0rem;
                    font-size: 3.2rem;
                    background-color: #f5f5f5;
                    border-radius: 2.4rem;
                    width: 44.3rem;
    
                    .list {
                        &-title {
                            opacity: 0.5;
                        }
    
                        &-items {
                            margin-top: 2.8rem;
                            line-height: 1.48;
                            margin-left: 4rem;

                            li::before {
                                font-size: 0.5rem;
                            }
                        }
                    }
                }
            }
        }

        #contact {
            padding: 6rem 20% 8rem;
            border-top: 1px solid black;

            #social-links {
                display: flex;
                justify-content: space-evenly;

                .social-link {
                    text-decoration: none;

                    &-text {
                        font-size: 2.8rem;

                        .social-icon {
                            margin-right: 0.6rem;
                        }
                    }
                }

                &:hover {
                    text-decoration: underline;
                }
            }
        }
    }
}